.button-defaults {
  background-color: white;
  white-space: pre;
  color: gray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.square-button {
  background: none;
  color: gray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 48px;
  height: 48px;
  -ms-flex-pack: center;
      justify-content: center;
}

.square-button:hover {
  -webkit-transform: scale(1.05, 1.05);
      -ms-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
  cursor: pointer;
}

.panel-button {
  background: none;
  color: gray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 1em 0 1em;
  -ms-flex-pack: center;
      justify-content: center;
}

.panel-button:hover {
  cursor: pointer;
}

.rectangle-button {
  background-color: white;
  font-weight: bold;
  border-width: 1px;
  border-color: lightgray;
  border-style: solid;
  color: gray;
  width: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 48px;
}

.rectangle-button:hover {
  -webkit-transform: scale(1.05, 1.05);
      -ms-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
  cursor: pointer;
}


