.home-container {
  background-color: white;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 40% 60%;
  -ms-flex-align: center;
      align-items: center;
}

.left-subpanel {
  background-color: whitesmoke;
  height: 100vh;
  padding: 0 10% 0 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.right-subpanel {
  padding: 15% 15% 15% 10%;
}

h1 {
  text-align: right;
  font-size: 3rem;
  font-weight: bold;
}

#blog-link {
  color: gray;
  text-align: right;
  text-decoration: underline;
  font-size: 6rem;
  font-weight: bold;
}

#github-button {
  max-width: 300px;
  width: auto;
  float: right;
  margin-top: 5%;
}


