.slider {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
}

.slider-navigation {
  width: 100%;
  height: 50px;
  position: relative;
}

.slider-navigation-index {
  width: 100%;
  height: 100%;
  z-index: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.slider-item {
  width: 100%;
  height: 90%;
  overflow-y: auto;
}

#next-arrow {
  position: absolute;
  right: 0;
  bottom: 0;
}

#prev-arrow {
  position: absolute;
  left: 0;
  bottom: 0;
}

#next-arrow, #prev-arrow {
  background-color: white;
  border-width: 1px;
  border-color: lightgray;
  border-style: solid;
  z-index: 1;
}

#radial-icon {
  width: 12px;
  height: 12px;
  margin: 2.5%;
}



